import React from "react";
import ProductCard from "./ProductCard/ProductCard.js";
import ProductCardLarge from "./ProductCardLarge/ProductCardLarge.js";
import ShopForm from "./ShopForm/ShopForm";
import "./Product.css";
import ProductCard10 from "./ProductCard10/ProductCard10.js"; 
import ProductCard20 from "./ProductCard20/ProductCard20.js"; 
import SoilChoice from "./SoilChoice.js"
import Carousel from 'react-bootstrap/Carousel'
import cartoon2 from "./cartoon2.png"
import DisplayCard from "./DisplayCard/DisplayCard.js";

const Product = ({
  setQuantity,
  bagQuantity,
  setQuantity10,
  bagQuantity10,
  setQuantity20,
  bagQuantity20,
  updateInterestedBasket,
  interested,
  onRouteChange,
  plantMix,
  setPlantMix,
  addToCart,
  arrCart
 
}) => {
 
  return (
    <div id="Product" className=" welcomeImgProducts">
      
      <h1 className="f1 bg-white title colourSecondary mt-0">40% SALE ON ALL PRODUCTS!</h1>
      <div className="card1"></div>
        <div className="m-5">  </div>

{/* Code to set the soil type of the product. */}
{/* <h2 className="f1 title colourSecondary tc o-0.5 b" >Choose your mix!</h2>
      <div className="marginAuto">
          <SoilChoice 
            plantMix= {plantMix}
            setPlantMix={setPlantMix}
            />  
      </div>
       */}
        
        {/* classname = " container " style={parent}  */}
        <div  className = 'centerPersonal container '  >
       

        <div className = " ">
        <Carousel
        variant="dark"
        controls = "1"
        touch = "true"
        indicators="true"
        classname = 'bg-white'
    
        >
        <Carousel.Item className = ' pa5'>
          <ProductCard
            setQuantity={setQuantity}
            bagQuantity={bagQuantity}
      
          />  
        </Carousel.Item>

        <Carousel.Item className = 'pa5'>
          <ProductCard20       
            setQuantity={setQuantity20}
            bagQuantity={bagQuantity20}
        
          />        
        </Carousel.Item>
        
        <Carousel.Item className = 'pa5'>
          <ProductCard10       
            setQuantity={setQuantity10}
            bagQuantity={bagQuantity10}
        
          />        
        </Carousel.Item>

               
        <Carousel.Item className = 'pa5'>
        <ProductCardLarge
              updateInterestedBasket={updateInterestedBasket}
              interested = {interested}
             
            />
        </Carousel.Item>

        </Carousel>
        </div>
            
        <div className="centerPersonal pv4  mh5 ">
         

              <ShopForm
              arrCart = {arrCart}
              bagQuantity={bagQuantity}
              bagQuantity10 = {bagQuantity10}
              bagQuantity20 = {bagQuantity20}
              interested={interested}
              onRouteChange={onRouteChange}
            />
        </div>

        </div>

        

      <div className="m-5">  </div>
      <div className="card1"></div>
    </div>
  );

  // style={{ height: 250, width: 250 }}
};
export default Product;
