import React from "react";
import bag from "../Products/ShopForm/bag.png";
import large from "../Products/ShopForm/large.png";
import deliveryPic from "../Products/ShopForm/delivery.jpg";
import emailjs from "emailjs-com";
import "./Checkout.css";
import BankDetails from "./BankDetails/BankDetails";
import soilPrice, { soilPrice10, soilPrice20 } from "../../res";

import ClientDetails from "./ClientDetails.js";
import cartoon2 from "./cartoon2.png"
import { getDefaultNormalizer } from "@testing-library/react";
import { MDBContainer, MDBRow, MDBCol, MDBBtn, MDBInput } from 'mdbreact';



class Checkout extends React.Component{

  

  constructor (){
    super();
    this.state = {
        email: '',
        name: '',
        address: '',
        suburb:'',
        postal:'',
        contact: '',
        message: '',
        invNo:'',
        disabled:false,
        fullAddress:'',
        

                }
}



componentDidMount() {
  window.scrollTo(0, 0)
}

onEmailChange = (event) => {
  this.setState({ email: event.target.value });
}
onNameChange = (event) => {
  this.setState({ name: event.target.value });
}
onAddressChange = (event) => {
  this.setState({ address:event.target.value },()=>{ this.onFullAddressChange() });
  
}
onSuburbChange = (event) => {
  this.setState({ suburb:event.target.value }, ()=>{ this.onFullAddressChange() });
}
onPostalChange = (event) => {
  this.setState({ postal:event.target.value }, ()=>{ this.onFullAddressChange() });
}
// Takes the address, suburb and postal  to create one address string with all the info, Updated every time a change is made to any of the component states
onFullAddressChange = () => {
  const {  address,suburb,postal} = this.state;
  const fullAddress = address + ', ' + suburb + ', ' + postal;
  this.setState({ fullAddress:fullAddress },()=>{  console.log(this.state.fullAddress) });

}
onContactChange = (event) => {
  this.setState({ contact: event.target.value });
}
onMessageChange = (event) => {
  this.setState({ message: event.target.value });
}
setInvNo = (data) => {
  this.setState({ invNo: data });
}


sendEmail = () => {
  const { bagQuantity,bagQuantity10,bagQuantity20,delivery, interested } = this.props;
  const { invNo, email, name,  message,fullAddress} = this.state;

  // creates strings that will be passed to the emailed invoice
  var interestedString = '';
  var date = new Date();
  var deliveryString = "";
  var collectionString ="";
  
  // use the delivery prop to create a string for the invoice that will clarify the order for delivery or collection
  if (delivery) {
    deliveryString = 'FOR DELIVERY';
  } else {
    collectionString = "FOR COLLECTION";
  }

  //gets todays date for the invoice
  date =  date.getFullYear()+'-'+(date.getMonth()+1)+'-'+date.getDate();
  
  // determines wether the buyer is interested in a large scale order or not. 
  if (interested) {
    interestedString = 'I am interested in a large scale order.';
  } else {
    interestedString = '';
  }
 
  // Builds the invoice parameters object that will be passed to email.js with all the necessary strings
var invParams = {

  invNo : invNo,
  email: email,
  name:name,
  address: fullAddress,
  message: message,
  bagQuantity: bagQuantity,
  bagQuantity10:bagQuantity10,
  bagQuantity20:bagQuantity20,
  total:this.props.totalCost,
  collection: collectionString,
  delivery:deliveryString,
  deliveryCost:this.props.deliveryCost,
  interested: interestedString,
  date: date
}
//initiatess the api to email js, It includes the user info, the invoice template and the invoice parameters.
emailjs
  .send(
    "service_1n01s4n",
    "Invoice_p6d7nb4",
    invParams,
    "user_CjJwAVXqSVa4P9xqDEp9o"
  )
  //Logs whether the invoice sent succesfully
  .then(
  (result) => {
    console.log('from send email', result.text);
  }
);
}

// onPlaceOrder defines the method for the confirm order button.
onPlaceOrder = (e) => {
   console.log('placeing order here');
  const { bagQuantity, interested, bagQuantity10,bagQuantity20 } = this.props;
  const { email, name, contact, message,fullAddress} = this.state;

  //calls one final check to make sure all the address info is captured, I was encountering some difficulties with states changing asychoronusly
  this.onFullAddressChange();
  var hasError = false;
// I notice there are two instances where I define Interested string. One for sendig email and one for the dataBase, Must look into this!
  var interestedString = '';
  if (interested) {
    interestedString = 'I am interested in a large scale order.';
  } 

  // This is the api for logging the invoice into the database,
  e.preventDefault();  
  fetch('https://secure-shelf-32680.herokuapp.com/checkout', {
    method: 'post',
    headers:{ 'Content-Type': 'application/json'}, 
    //the abody that has all of the necessary info for the record.
    body: JSON.stringify({
      
        email: email,
        name: name,
        contact: contact,
        address: fullAddress,
        message: message,
        bagQuantity:bagQuantity,
        bagQuantity10:bagQuantity10,
        bagQuantity20:bagQuantity20,
        interested: interestedString, 
    
        deliveryCost : this.props.deliveryCost,
        total: this.props.totalCost
    })
    
  })
  .then(response => response.json())
  .then( invoice =>  {
    //I then recive the new invoice number from the Database and set it to the state value.
    this.setState({ invNo: invoice });
    console.log(bagQuantity)
  })
  .then(response => {
    //If the invNo is a valid output then It will initiate the Email protocal
    if (this.state.invNo !== 'cannot add client or Invoice' ){
    this.sendEmail();

    }else{
      //if there was an error on the database side and it was unable to add the record then an error message will show.
      console.log('failed to add to database', this.state.invNo);
      hasError=true;
    }
  })
  .then(
    (result) => {   
      //If there is stil no error then an alert will pop up confirming the order. 
      if (!hasError){   
      this.props.onRouteChange("thank");
      alert("Thank you, your order has been recieved! If you do not recive and email please contact us at joumasesoil@gmail.com .");
    }})
    .catch(   (error) => {
      //If there was an error then an alert will pop up informing the user
      console.log('my error messsage',error, 'body: ', this.body);
      alert(
        "Sorry there was an error, please try again or contact us at joumasesoilcpt@gmail.com"
          );
    })
}



render(){
    const {bagQuantity20, bagQuantity10, bagQuantity, interested, onRouteChange, route, delivery} = this.props;
  
  
    
    return (

        <div className="w-100 welcomeImgOut" >
        {/* IF ROUTE = THANK THEN SHOW BANK DETAILS AND THANK YOU ELSE SHOW SHOPPING CARD AND DETAILS CARD */}
        {route === "thank" ? (
          <div>
            <div className='f2 '>Thanks mate, happy growing!</div>
            <div className='f4 pa4 '>Please ensure your email address is correct: {(this.state.email)}</div>
            <BankDetails />
  
          </div>
        ) : (
          // SHOPPING BASKET FIRST
          <div id='basket' className="centerPersonal mv1  container">
            <div className="centerPersonal br2 shadow-5 b--black-10 contact-form ma3 bg-white ">
              <h1 className="ma1 pa3 b colourPrimary">Shopping Basket</h1>
            
              <ul className="list  mt0 pa2 w-100 measure center">

              <div className = "card1"> </div>
              {
               (bagQuantity>0)&&(
                <li className="flex items-center lh-copy  ph0-l bb b--black-10 padsides">
                  <img
                    className="w2 h2 w3-ns h3-ns pa3"
                    src={bag}
                    alt=""
                  />
                  <div className="pl3 flex-auto">
                    <span className="f6 db black-70">30lt bag</span>
                    <span className="f6 db black-70">R{soilPrice}</span>
                  </div>
                  <div>
                    <p className="f6 link hover-dark-gray pa3">
                      x{bagQuantity}
                    </p>
                  </div>
                </li>
                )}

                { (bagQuantity20 > 0) && (
                    <li className="flex items-center lh-copy  ph0-l bb b--black-10 padsides">
                    <img
                      className="w2 h2 w3-ns h3-ns pa3"
                      src={bag}
                      alt=""
                    />
                    <div className="pl3 flex-auto">
                      <span className="f6 db black-70">20lt bag</span>
                      <span className="f6 db black-70">R{soilPrice20}</span>
                    </div>
                    <div>
                      <p className="f6 link hover-dark-gray pa3">
                        x{bagQuantity20}
                      </p>
                    </div>
                  </li>
                )}

                { (bagQuantity10 > 0) && (
                    <li className="flex items-center lh-copy  ph0-l bb b--black-10 padsides">
                    <img
                      className="w2 h2 w3-ns h3-ns pa3"
                      src={bag}
                      alt=""
                    />
                    <div className="pl3 flex-auto">
                      <span className="f6 db black-70">10lt bag</span>
                      <span className="f6 db black-70">R{soilPrice10}</span>
                    </div>
                    <div>
                      <p className="f6 link hover-dark-gray pa3">
                        x{bagQuantity10}
                      </p>
                    </div>
                  </li>
                )}

                {interested && (
                  <li className="flex items-center lh-copy ph0-l bb b--black-10 padsides">
                    <img
                      className="w2 h2 w3-ns h3-ns br-100 pa3"
                      src={large}
                      alt=""
                    />
                    <div className="pl3 flex-auto">
                      <span className="f6 db black-70">500lt +</span>
                      <span className="f6 db black-70">Living soil</span>
                    </div>
                    <div>
                      <p className="f6 link hover-dark-gray pa3">
                        Interested
                      </p>
                    </div>
                  </li>
                )}

              {delivery && (
                  <li className="flex items-center lh-copy ph0-l bb b--black-10 padsides">
                    <img
                      className="w2 h2 w3-ns h3-ns pa3"
                      src={deliveryPic}
                      alt=""
                    />
                    <div className="pl3 flex-auto">
                      <span className="f6 db black-70">Delivery fee</span>
                      <span className="f6 db black-70">R150 per 30L</span>
                    </div>
                    <div>
                      <p className="f6 link hover-dark-gray pa3">
                        R{this.props.deliveryCost}
                      </p>
                    </div>
                  </li>
                )}

              <div className = "card1"> </div>  

              <li className="flex items-center lh-copy  ph0-l bb b--black-10 padsides">
                
                  <div className="pl3 flex-auto">
                    <span className="f6 db black-70 b">TOTAL:</span>
            
                  </div>
                  <div>
                    <p className="f6 link  hover-dark-gray pa3">
                      R{this.props.totalCost}.00
                    </p>
                  </div>
                </li>
  
              </ul>
            </div>
            {/* DETAILS FORM, NEEDS TO GET UPDATED TO LOOK BETTER, LOOKS LIKE SHIT  Wooo IT looks better now*/}
            <div className=" mv3 w-100 centerDetails ">
              <ClientDetails
                className = ""
                onPlaceOrder = {this.onPlaceOrder}
                onNameChange = {this.onNameChange}
                onEmailChange = {this.onEmailChange}
                onContactChange = {this.onContactChange}
                onAddressChange = {this.onAddressChange}
                onSuburbChange = {this.onSuburbChange}
                onPostalChange = {this.onPostalChange}
                onMessageChange = {this.onMessageChange}
                delivery = {this.props.delivery}
                setDelivery = {this.props.setDelivery}
                setTotalCost = {this.setTotalCost}
            />
            </div>
          
            <BankDetails />  
            
          </div>
        )}
      </div>
    );

  }

};

export default Checkout;
