import React from "react";
import indoor from "./indoor.png"
import cannaMix from "./cannaMix.jpg"

class SoilChoice extends React.Component {
    
    render() {
        const {plantMix , setPlantMix} =this.props;
        return(
            <div>
                <div className="card mb-3 w-100" >
                <div className="card-header">
                    <div className="form-check form-check-inline">
                        <input 
                            className="form-check-input" 
                            type="radio" 
                            name="inlineRadioOptions" 
                            id="inlineRadio1" 
                            onClick= {()=> this.props.setPlantMix("indoor")}
                            />
                        
                        <label className="form-check-label colourBlue b" for="inlineRadio1" >Indoor Plant Mix</label>
                    </div>
                    <div className="form-check form-check-inline">
                        <input 
                            className="form-check-input" 
                            type="radio" 
                            name="inlineRadioOptions" 
                            id="inlineRadio2" 
                            onClick= {()=> this.props.setPlantMix("cannaMix")}
                            />
                        <label className="form-check-label colourPrimary b" for="inlineRadio2">CannaMix</label>
                    </div>
                </div>
                    <div className="row g-0">
                       <div className="col-md-4 h4">
                            {(plantMix==="indoor")? ( 
                                <img
                                    src= {indoor}
                                    alt="..."
                                    className="img-fluid db w-100 br2 br--top h4"
                                />
                            ) : (
                                <img
                                    src= {cannaMix}
                                    alt="..."
                                    className="img-fluid"
                                />
                            )}

                            
                        </div>
                        <div className="col-md-8">
                        {(plantMix==="indoor")?(
                            <div className="card-body">
                            <h5 className="card-title">Indoor Potting Mix</h5>
                            <p className="card-text">
                            This is a wider card with supporting text below as a natural lead-in to
                            additional content. This content is a little bit longer.
                            </p>
                            <p className="card-text">
                            <small className="text-muted">Last updated 3 mins ago</small>
                            </p>
                        </div>

                        ):(
                            <div className="card-body">
                                <h5 className="card-title">CannaMix</h5>
                                <p className="card-text">
                                This is a wider card with supporting text below as a natural lead-in to
                                additional content. This content is a little bit longer.
                                </p>
                                <p className="card-text">
                                <small className="text-muted">Last updated 3 mins ago</small>
                                </p>
                            </div>
                        )}    
                            

                        </div>
                    </div>
                </div>
    </div>
        )
        
    }
    }
    export default SoilChoice