import React, { Component } from "react";
import 'mdbreact/dist/css/mdb.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn, MDBCard, MDBCardBody } from 'mdbreact';
import "./ClientDetails.css";

class ClientDetails extends Component {
 
  constructor() {
    super();
    this.state= {
      disabled:false,
      
    }
  }

  setDisabled = (data) => {
    this.props.onPlaceOrder(data);
    this.setState({ disabled: true });
  }
  


  render(){
  const {onPlaceOrder,
          onNameChange, 
          onEmailChange, 
          onContactChange, 
          onAddressChange, 
          onSuburbChange, 
          onPostalChange, 
          onMessageChange, 
          setDelivery,
          delivery
        }= this.props;

  return (
    


    <MDBContainer className="">
      <MDBRow className="" > 
        <MDBCol md="9" className="">
          <MDBCard className="">
            <MDBCardBody className="">
              <form className='white ph2 mt3 ' onSubmit={this.setDisabled}>
  
                <div className="grey-text  ">
                  <MDBInput

                    className = ""
                    label="Your name"
                    icon="user"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    containerClass="text-left"
                    onChange={onNameChange}
                    required
                  />
                  <MDBInput
                    label="Your email"
                    icon="envelope"
                    group
                    type="email"
                    validate
                    error="wrong"
                    success="right"
                    containerClass="text-left"
                    onChange={onEmailChange}
                    required
                  />
                    <MDBInput
                    label="Contact"
                    icon="phone"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    containerClass="text-left"
                    onChange={onContactChange}
                  />
                  <MDBInput
                    label="Message to Seller"
                    icon="message"
                    group
                    type="text"
                    validate
                    containerClass="text-left"
                    onChange={onMessageChange}
                  />

<div className=" form-check b ">
  <input
    className="form-check-input"
    type="radio"
    name="radioDefault"
    id="radioDelivery"
    onClick= {()=> this.props.setDelivery(true)}
  />
  <label className="form-check-label" for="radioDelivery"> Delivery </label>
</div>

<div className="form-check b ">
  <input
    className="form-check-input"
    type="radio"
    name="radioDefault"
    id="radioPickUp"
    check 
    onClick= {()=> this.props.setDelivery(false)}
  />
  <label className="form-check-label" for="radioPickUp"> Pick Up </label>
</div>
{(this.props.delivery) ? (
  <div>
                  <MDBInput
                    label="Address: Line 1"
                    icon="home"
                    group
                    type="text"
                    validate
                    required
                    error="wrong"
                    success="right"
                    containerClass="text-left"
                    onChange={onAddressChange}
                  />
                  <MDBInput
                    label="Address: Line 2"
                    icon="blank"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    containerClass="text-left"
                    onChange={onSuburbChange}
                  />
                  <MDBInput
                    label="Postal Code"
                    icon="blank"
                    group
                    type="text"
                    validate
                    required
                    error="wrong"
                    success="right"
                    containerClass="text-left"
                    onChange={onPostalChange}
                  />
                  </div>
                  ):(
                  <div className= "tl">
                    <span>
                    <span className="b colourPrimary">  To arrange collection from: <br/></span>
                    <span className = "text-black"> 
                      9 Garden Drive <br/>
                      Sunningdale, Cape Town <br/>
                      contact Ruan:<span className= "colourBlue b"> 076 799 0801<br/> </span>
                    </span>
                    <span className="b colourPrimary">  To arrange collection from:<br/> </span>
                    <span className = "text-black"> 
                      6 Queen road <br/>
                      Rondebosch, Cape Town <br/>
                      contact Julian:<span className= "colourBlue b"> 081 845 3640 </span>
                      </span>
                    </span>
                    </div>
                  )}
                  
                </div>
{/* LOADING BAR FOR WHEN THE BUTTON IS CLICKED */}
                { this.state.disabled ? (
                  <div
                  className="f5 btnC btn-primaryC btn-xlC pointer ma5 pa5 greyBck"
                  >
                      <div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
                  </div>

                ):(
                <div className="text-center py-4 mt-3">
                  <MDBBtn color="cyan" 
                    className='btn btn-outline-cyan'
                    disabled = {(this.state.disabled)? "disabled" : ""}
                    data-mdb-ripple-color="dark"
                    //onClick={this.setDisabled}
                    type="submit"
                    >
                    Place Order
                  </MDBBtn>
                </div>
                )}
                
              </form>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  );
};
}

export default ClientDetails;