import "./App.css";
import Navigation from "./Components/Navigation/Navigation";
import Welcome from "./Components/Welcome/Welcome";
import About from "./Components/About/About";

import Product from "./Components/Products/Product";
import bag from "./photos/bag.jpeg"
import "tachyons";
import { Component } from "react";
import Contacts from "./Components/Contacts/Contacts";
import Checkout from "./Components/Checkout/Checkout";
import AboutDesktop from "./Components/About/AboutDesktop";
import soilPrice, { soilPrice10, soilPrice20 } from "./res";

class App extends Component {
  constructor() {
    super();
    this.state = {
      bagQuantity: 1,
      bagQuantity10 : 0,
      bagQuantity20 : 0,
      interested: false,
      delivery : true,
      deliveryCost:0,
      totalCost:0,
      plantMix: "indoor",
      route: "home",
      arrCart : [
        { name : 'Indoor Potting',
          size : '30',
          quantity : '2'
        },
        { name : 'canna mix',
          size : '20',
          quantity : '1',
        }
      ],
      iNewItem : 0


      
    };
  }


  
  // addCartItem = (data) => {
  //   objCart.iNewItem = "New Item Value";
  //   inc(iNewItem);
  //   };
  

  addToCart = (data) => {
    console.log(data);
    this.setState(prevState => ({
      arrCart: [...prevState.arrCart, data] 
    }));
  }

  setPlantMix = (data) => {
    this.setState({plantMix:data});
  }

  updateInterestedBasket = (data) => {
    this.state.interested
      ? this.setState({ interested: false })
      : this.setState({ interested: true });
  };

  setDelivery = (data) => {
    this.setState({delivery:data}, function(){this.setTotalCost();
    })
  };

  setDeliverycost  = (data) => {
    this.state.delivery
    ? this.setState({ deliveryCost : 150*Math.ceil((10*this.state.bagQuantity10+20*this.state.bagQuantity20+30*this.state.bagQuantity)/30)}, function() { this.setState({totalCost: (this.state.bagQuantity*soilPrice + this.state.bagQuantity20*soilPrice20 + this.state.bagQuantity10*soilPrice10 + this.state.deliveryCost)})}
    )
    : this.setState({ deliveryCost : 0 },function() { this.setState({totalCost: (this.state.bagQuantity*soilPrice + this.state.bagQuantity20*soilPrice20 + this.state.bagQuantity10*soilPrice10 + this.state.deliveryCost)})});
    
  };
  
  setTotalCost = (data) => {
    this.setDeliverycost();
  
  };
  

  setQuantity = (data) => {
    console.log('here')
    if (data.target.className.includes('plus')) {
      this.setState((prevState) => {
        return { bagQuantity: prevState.bagQuantity + 1 };
      });
    } else {
      this.state.bagQuantity <= 1
        ? this.setState({ bagQuantity: 0 })
        : this.setState((prevState) => {
            return { bagQuantity: prevState.bagQuantity - 1 };
          });
    }
  };

  setQuantity10 = (data) => {
    console.log('here')
    if (data.target.className.includes('plus')) {
      this.setState((prevState) => {
        return { bagQuantity10: prevState.bagQuantity10 + 1 };
      });
    } else {
      this.state.bagQuantity10 <= 1
        ? this.setState({ bagQuantity10: 0 })
        : this.setState((prevState) => {
            return { bagQuantity10: prevState.bagQuantity10 - 1 };
          });
    }
  };

  setQuantity20 = (data) => {
    console.log('here')
    if (data.target.className.includes('plus')) {
      this.setState((prevState) => {
        return { bagQuantity20: prevState.bagQuantity20 + 1 };
      });
    } else {
      this.state.bagQuantity20 <= 1
        ? this.setState({ bagQuantity20: 0 })
        : this.setState((prevState) => {
            return { bagQuantity20: prevState.bagQuantity20 - 1 };
          });
    }
  };



  onRouteChange = (route) => {
    this.setState({ route: route });
    this.setTotalCost();
  };
  

  //comment
  render() {
    return (
      <div className="App">
        <Navigation
          onRouteChange={this.onRouteChange}
          interested={this.state.interested}
          route={this.state.route}
        />

        {this.state.route === "home" ? (
          <div>
            <Welcome />
            <Product
              setQuantity={this.setQuantity}
              bagQuantity={this.state.bagQuantity}
              setQuantity10={this.setQuantity10}
              bagQuantity10={this.state.bagQuantity10}
              setQuantity20={this.setQuantity20}
              bagQuantity20={this.state.bagQuantity20}

              addToCart={this.addToCart}
              arrCart = {this.state.arrCart}
              plantMix = {this.state.plantMix}
              setPlantMix = {this.setPlantMix}
              updateInterestedBasket={this.updateInterestedBasket}
              interested={this.state.interested}
              onRouteChange={this.onRouteChange}
            />
            
          </div>
        ) : this.state.route === "about" ?(
          <div>
            { window.screen.width < 500 ? (
                <About id="About" />
            ) : (
                <AboutDesktop id="About" />
            )}
           
          </div>
          
        ): (
            <Checkout
                        onRouteChange={this.onRouteChange}
                        bagQuantity={this.state.bagQuantity}
                        bagQuantity10={this.state.bagQuantity10}
                        bagQuantity20={this.state.bagQuantity20}
                        interested={this.state.interested}
                        delivery = {this.state.delivery}
                        setDelivery = {this.setDelivery}
                        setDeliverycost = {this.setDeliverycost}
                        setTotalCost = {this.setTotalCost}
                        deliveryCost = {this.state.deliveryCost}
                        totalCost = {this.state.totalCost}
                        route={this.state.route}
                      />
        )
        }

        <Contacts />
      </div>
    );
  }
}

export default App;
